'use client';

import React, { useState, useEffect } from 'react';
import { Moon, Sun } from 'lucide-react';

export default function ThemeToggle() {
    const [isDark, setIsDark] = useState(false);
    const [mounted, setMounted] = useState(false);

    useEffect(() => {
        setMounted(true);
        // Î°úÏª¨ Ïä§ÌÜ†Î¶¨ÏßÄÏóêÏÑú ÌÖåÎßà ÏÑ§Ï†ï Î∂àÎü¨Ïò§Í∏∞
        const savedTheme = localStorage.getItem('theme');
        const prefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;

        if (savedTheme === 'dark' || (!savedTheme && prefersDark)) {
            setIsDark(true);
            document.documentElement.classList.add('dark');
        }
    }, []);

    const toggleTheme = () => {
        const newIsDark = !isDark;
        setIsDark(newIsDark);

        if (newIsDark) {
            document.documentElement.classList.add('dark');
            localStorage.setItem('theme', 'dark');
        } else {
            document.documentElement.classList.remove('dark');
            localStorage.setItem('theme', 'light');
        }
    };

    // SSR ÍπúÎπ°ÏûÑ Î∞©ÏßÄ
    if (!mounted) {
        return (
            <button className="p-2 rounded-full bg-muted/50 w-9 h-9" aria-label="ÌÖåÎßà Ï†ÑÌôò">
                <span className="sr-only">ÌÖåÎßà Î°úÎî©Ï§ë</span>
            </button>
        );
    }

    return (
        <button
            onClick={toggleTheme}
            className="p-2 rounded-full bg-muted/50 hover:bg-muted transition-all duration-300 group"
            aria-label={isDark ? 'ÎùºÏù¥Ìä∏ Î™®ÎìúÎ°ú Ï†ÑÌôò' : 'Îã§ÌÅ¨ Î™®ÎìúÎ°ú Ï†ÑÌôò'}
            title={isDark ? 'ÎùºÏù¥Ìä∏ Î™®Îìú' : 'Îã§ÌÅ¨ Î™®Îìú'}
        >
            {isDark ? (
                <Sun className="w-5 h-5 text-yellow-400 group-hover:rotate-90 transition-transform duration-300" />
            ) : (
                <Moon className="w-5 h-5 text-slate-600 group-hover:-rotate-12 transition-transform duration-300" />
            )}
        </button>
    );
}
